.gallery-section {
  padding-top: 20px;
  padding-bottom: 40px;
  @media screen and (min-width: 768px) {
    padding-top: 60px;
    padding-bottom: 50px;
  }
}

.sign-in-wrapper {
  display: flex;
  justify-content: center;
}

#img-sign-in {
  border-radius: 29px;
}

.gallery__list {
  display: flex;
  flex-wrap: wrap;
  row-gap: 20px;
  @include tablet {
    row-gap: 30px;
    column-gap: 30px;
    margin-right: -30px;
  }
  @include desktop {
    row-gap: 30px;
    column-gap: 15px;
    margin-right: -15px;
  }
}

.gallery__list--library {
  display: flex;
  flex-wrap: wrap;
  row-gap: 20px;

  @include tablet {
    row-gap: 30px;
    column-gap: 30px;
    margin-right: -30px;
  }

  @include desktop {
    row-gap: 30px;
    column-gap: 15px;
    margin-right: -15px;
  }
}

.gallery__item {
  background-color: transparent;
  border-radius: 5px;
  transition: box-shadow, scale 250ms cubic-bezier(0.075, 0.82, 0.165, 1);

  &:hover,
  &:focus {
    box-shadow: 10px 7px 13px -6px rgba(255, 107, 8, 0.72);
    scale: 1.02;
  }
  @include tablet {
    flex-basis: calc((100% - 2 * 30px) / 2);
  }
  @include desktop {
    flex-basis: calc((100% - 3 * 15px) / 3);
  }
}
.gallery-card__main {
  position: relative;
}
.gallery-card__rating {
  position: absolute;
  top: 10px;
  right: 15px;
  @include font($primary-font-size, 1.16, 500, none);
  color: $lighter-text-color;
  background-color: rgba(255, 107, 1, 0.66);
  border-radius: 5px;
  padding: 4px 8px;
  @include desktop {
    padding: 6px 12px;
  }
}
.gallery-card__content {
  margin-top: 4px;
  padding: 4px;
  @include desktop {
    padding: 6px;
    margin-top: 6px;
  }
}
.gallery-card__img {
  background-color: $footer-background-color;

  border-radius: 5px;
}

.gallery-card__title {
  text-transform: uppercase;
  @include font($primary-font-size, 1.16, 500, none);
  font-style: normal;
  font-family: 'Roboto';
  @include tablet {
    @include font(none, 1.33, none, none);
  }
  @include desktop {
    @include font(20px, 1.2, none, none);
  }
}
.gallery-card__genre {
  @include font($primary-font-size, 1.3, none, none);
  font-style: normal;
  font-family: 'Roboto';
  color: $accent-color;
  @include desktop {
    @include font(20px, 1.2, none, none);
  }
}
